@charset "UTF-8";
@import url("https://fonts.googleapis.com/css2?family=Inter:wght@400;600&display=swap");
* {
  box-sizing: border-box;
  margin: 0;
  scroll-behavior: smooth;
  background-color: #E1D4C9;
  font-family: "Inter", sans-serif;
  font-feature-settings: "clig" off, "liga" off;
  font-style: normal;
}

body {
  width: 100%;
  overflow-x: hidden;
}

.container {
  max-width: 1440px;
  margin: 0 auto;
  padding: 0 40px;
}

section {
  margin-top: 100px;
}

h2 {
  color: #403F3D;
  font-feature-settings: "clig" off, "liga" off;
  font-size: 60px;
  font-weight: 600;
  line-height: 125%;
}
h2 span {
  color: #B0907A;
  font-style: italic;
}

.app-button {
  text-decoration: none;
  display: flex;
  width: 200px;
  height: 64px;
  padding: 12px 20px;
  align-items: center;
  gap: 8px;
  border-radius: 100px;
  border: 1px solid #665F55;
  color: #403F3D;
  transition: color 0.5s ease, background-color 0.5s ease;
}
.app-button * {
  transition: color 0.5s ease, background-color 0.5s ease;
}
.app-button_descr {
  font-feature-settings: "clig" off, "liga" off;
  font-weight: 600;
}
.app-button_available {
  font-size: 10px;
  line-height: 140%;
}
.app-button_platform {
  font-size: 16px;
  line-height: 150%;
}
.app-button:hover {
  color: #E1D4C9;
  background-color: #665F55;
}
.app-button:hover * {
  color: #E1D4C9;
  background-color: #665F55;
}
.app-button svg path {
  transition: 0.5s all;
}
.app-button:hover svg path {
  fill: #E1D4C9;
}

.social {
  display: flex;
  width: 60px;
  height: 60px;
  justify-content: center;
  align-items: center;
  border-radius: 100px;
  border: 1px solid #C1B6AD;
  transition: 0.5s all;
}
.social svg path {
  transition: 0.5s all;
}
.social:hover {
  background: #E1D4C9;
}
.social:hover svg path {
  stroke: #403F3D;
}

.header {
  height: 60px;
  margin: 20px 0;
}
.header_wrapper {
  display: flex;
  justify-content: space-between;
  height: 100%;
  min-height: 60px;
  align-items: center;
}
.header_logo-hamburger-wrapper {
  height: 100%;
  display: flex;
  justify-content: space-between;
}
.header_logo-hamburger-wrapper .hamburger {
  display: none;
}
.header_list {
  display: flex;
  list-style-type: none;
  padding: 0;
  column-gap: 40px;
}
.header_item {
  border-bottom: 1px solid transparent;
  transition: 0.5s all;
}
@media (hover: hover) {
  .header_item:hover {
    border-bottom: 1px solid #403F3D;
  }
}
.header .menu-link_link {
  display: flex;
  align-items: center;
  gap: 8px;
  border-bottom: 1px solid transparent;
  transition: 0.5s all;
}
.header .menu-link_link:hover {
  border-bottom: 1px solid #403F3D;
}

.link-text {
  color: #403F3D;
  font-feature-settings: "clig" off, "liga" off;
  font-size: 16px;
  font-style: normal;
  font-weight: 600;
  line-height: 150%;
  text-decoration: none;
}

.enjoy {
  color: #E1D4C9;
  margin-top: 20px;
  padding: 100px;
  border-radius: 40px;
  overflow: hidden;
  position: relative;
}
.enjoy_video {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  overflow: hidden;
  display: block;
  z-index: 0;
}
.enjoy * {
  background-color: transparent;
}
.enjoy_hero {
  position: relative;
  z-index: 2;
  display: flex;
  max-width: 530px;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  align-self: stretch;
  display: flex;
  gap: 40px;
  border-radius: 40px;
}
.enjoy_title {
  margin: 0;
  font-size: 72px;
  font-weight: 600;
  line-height: 105%;
}
.enjoy_title span {
  color: #B0907A;
  font-style: italic;
}
.enjoy_descr {
  font-size: 16px;
  font-weight: 400;
  line-height: 150%;
}
.enjoy_button {
  position: relative;
  display: flex;
  gap: 8px;
  justify-content: center;
  height: 64px;
  width: 200px;
  border-radius: 100px;
  background: #E1D4C9;
  text-align: center;
  padding: 20px 78px;
  transition: all 0.5s ease-out;
}
.enjoy_button span {
  transition: all 0.5s ease-out;
}
.enjoy_button:hover span {
  transform: translateX(-15%);
}
.enjoy_button svg {
  overflow: hidden;
}
.enjoy_button svg path {
  transition: all 0.5s ease-out;
  stroke: transparent;
}
.enjoy_button:hover svg {
  overflow: visible;
}
.enjoy_button:hover svg path {
  stroke: #403F3D;
  display: block;
}

.favorites {
  text-align: center;
}

.slider {
  margin-top: 40px;
  box-sizing: border-box;
  color: #403F3D;
  font-feature-settings: "clig" off, "liga" off;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.slider * {
  box-sizing: border-box;
}
.slider_wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 640px;
}
.slider_items {
  overflow: hidden;
  width: 480px;
}
.slider_line {
  display: flex;
  transition: 0.5s transform;
}
.slider_arrow {
  border-radius: 100px;
  border-style: solid;
  border-color: #665F55;
  border-width: 1px;
  display: flex;
  flex-direction: row;
  gap: 0px;
  align-items: center;
  justify-content: center;
  flex: 0 0 auto;
  width: 60px;
  height: 60px;
  position: relative;
  transition: all 0.5s;
}
.slider_arrow:hover {
  background: #665F55;
}
.slider_arrow svg {
  transition: all 0.5s;
}
.slider_arrow:hover svg {
  background-color: #665F55;
}
.slider_arrow:hover svg path {
  stroke: #E1D4C9;
}
.slider .slide_img {
  max-height: 480px;
}
.slider .slide_descr {
  width: 100%;
  text-align: center;
  margin-top: 20px;
}
.slider .slide_title {
  margin-top: 20px;
  font-size: 24px;
  font-weight: 600;
  line-height: 125%;
}
.slider .slide_text {
  margin-top: 16px;
  font-size: 16px;
  font-weight: 400;
  line-height: 150%;
}
.slider .slide_price {
  margin-top: 16px;
  font-size: 24px;
  font-weight: 600;
  line-height: 125%;
}
.slider .hidden {
  display: none;
}
.slider_controls {
  margin-top: 40px;
  display: flex;
  gap: 12px;
  justify-content: center;
}
.slider_controls .control {
  position: relative;
  width: 40px;
  height: 4px;
  border-radius: 100px;
  background: #C1B6AD;
}
.slider_controls .control_progress {
  position: absolute;
  left: 0;
  border-radius: 100px;
  width: 0;
  height: 4px;
  background-color: #665F55;
  transition: width 0.6s ease;
}

.about_title {
  font-size: 59.9px;
}

.gallery {
  margin-top: 40px;
  display: flex;
  gap: 40px;
  flex-wrap: wrap;
  justify-content: center;
}
.gallery_column {
  display: flex;
  flex-direction: column;
  gap: 40px;
}
.gallery_img {
  max-width: 660px;
  overflow: hidden;
  border-radius: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.gallery_img img {
  transform: scale(1.1);
  width: 100%;
  transition: transform 500ms ease;
}
.gallery_img:hover img {
  transform: scale(1);
  object-fit: cover;
}
.gallery_img__square {
  max-height: 590px;
}
.gallery_img__rectangle {
  max-height: 430px;
}

.mobile {
  margin-top: 100px;
}
.mobile_wrapper {
  display: flex;
  gap: 100px;
  justify-content: space-between;
  align-items: center;
  flex-wrap: nowrap;
}
.mobile_main {
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 40px;
}
.mobile_descr {
  line-height: 150%;
}
.mobile_apps {
  display: flex;
  gap: 20px;
}

.modal {
  display: none;
  z-index: 99;
  background: rgba(64, 63, 61, 0.8);
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
}
.modal-close {
  position: absolute;
  top: -52px;
  right: -43px;
  border-radius: 100px;
  cursor: pointer;
}
.modal-close:hover {
  cursor: pointer;
}
.modal-window {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  margin: 0;
  width: 900px;
  border-radius: 9px;
  box-shadow: 0 2px 35px 14px rgba(13, 13, 13, 0.04);
  background: #E1D4C9;
}
.modal-wrapper {
  display: flex;
}
.modal-img {
  border-radius: 9px 0 0 9px;
  width: 50%;
  /* Чтобы сохранить пропорции */
  object-fit: cover;
}
.modal-content {
  padding: 50px 20px 30px 30px;
  font-weight: 400;
  letter-spacing: 0.06em;
  color: #000;
  width: 350px;
}
.modal-content_header {
  font-size: 35px;
  line-height: 1.3;
}
.modal-content_subheader {
  padding-top: 10px;
  font-size: 20px;
  line-height: 1.15;
}
.modal-content_descr {
  font-size: 15px;
  line-height: 1.1;
  padding: 40px 0;
}
.modal-content_additional {
  padding-bottom: 10px;
  font-weight: 700;
  line-height: 1.1;
}
.modal-content_additional:before {
  content: "•";
  padding-right: 10px;
}
.modal-content_additional span {
  font-weight: 400;
}

.header_menu-link__underline {
  border-bottom: 1px solid #403F3D;
  cursor: default;
}

.menu {
  margin-top: 20px;
}
.menu_title {
  margin: 0 auto;
  max-width: 800px;
  text-align: center;
  color: #403F3D;
  font-size: 60px;
  font-weight: 600;
  line-height: 125%;
}
.menu_title span {
  color: #B0907A;
  font-style: italic;
}

.tab_buttons {
  margin-top: 40px;
  display: flex;
  justify-content: center;
  gap: 16px;
}
.tab_items {
  display: flex;
  justify-content: center;
}
.tab_content {
  margin-top: 40px;
  display: flex;
  flex-wrap: wrap;
  gap: 40px;
  justify-content: center;
}
.tab_refresh {
  display: flex;
  margin: 40px auto 0 auto;
  display: flex;
  width: 60px;
  height: 60px;
  justify-content: center;
  align-items: center;
  border-radius: 100px;
  border: 1px solid #665F55;
}

.menu-card {
  width: 310px;
  height: 506px;
  border-radius: 40px;
  border: 1px solid #C1B6AD;
  transition: all 0.5s;
}
.menu-card_img {
  width: 310px;
  height: 310px;
  flex-shrink: 0;
  overflow: hidden;
  border-radius: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.menu-card_img img {
  transform: scale(1.1);
  width: 100%;
  transition: all 0.5s;
}
.menu-card_img img:hover {
  transform: scale(1);
  object-fit: cover;
}
.menu-card_text {
  padding: 20px;
  display: flex;
  flex-direction: column;
  height: 196px;
  gap: 12px;
  color: #403F3D;
  background: transparent;
}
.menu-card_text .menu-card_price {
  margin-top: auto;
}
.menu-card_title {
  font-size: 24px;
  font-weight: 600;
  line-height: 125%;
}
.menu-card_descr {
  font-size: 16px;
  font-weight: 400;
  line-height: 150%;
}
.menu-card_price {
  font-size: 24px;
  font-weight: 600;
  line-height: 125%;
}
.menu-card:hover {
  cursor: pointer;
}
.menu-card:hover img {
  transform: scale(1);
  object-fit: cover;
}

.tab-button {
  display: flex;
  padding: 8px 16px 8px 8px;
  justify-content: center;
  align-items: center;
  gap: 8px;
  border-radius: 100px;
  border: 1px solid #C1B6AD;
  transition: all 0.5s;
}
.tab-button_img {
  width: 30px;
  height: 30px;
  display: flex;
  padding: 3px 7px;
  justify-content: center;
  align-items: center;
  border-radius: 100px;
  background: #C1B6AD;
}
.tab-button_img img {
  width: 16px;
  background: transparent;
  object-fit: cover;
}
.tab-button_descr {
  color: #403F3D;
  font-size: 16px;
  font-weight: 600;
  line-height: 150%;
  background: transparent;
}
.tab-button:hover {
  background: #665F55;
}
.tab-button:hover .tab-button_img {
  background: #E1D4C9;
}
.tab-button.active {
  background: #665F55;
}
.tab-button.active .tab-button_img {
  background: #E1D4C9;
}
.tab-button.active .tab-button_descr {
  color: #E1D4C9;
}

.footer {
  margin-top: 100px;
  margin-bottom: 40px;
}
.footer_wrapper {
  display: flex;
  padding: 100px;
  justify-content: center;
  align-items: center;
  gap: 100px;
  flex-wrap: wrap;
  border-radius: 40px;
  background: #665F55;
}
.footer_column {
  min-width: 380px;
  max-width: 600px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  gap: 40px;
  flex: 1 0 0;
  background-color: transparent;
  width: 568px;
}
.footer_column * {
  background-color: transparent;
}
.footer_title {
  margin-top: 0;
  color: #E1D4C9;
}
.footer_title span {
  color: #B0907A;
}
.footer_socials {
  display: flex;
  gap: 12px;
}
.footer_contact-us {
  color: #E1D4C9;
  font-size: 24px;
  font-weight: 600;
  line-height: 125%;
}
.footer_contacts {
  display: flex;
  flex-direction: column;
  gap: 16px;
  color: #E1D4C9;
  font-size: 16px;
  font-weight: 600;
  line-height: 150%;
}
.footer_contacts a {
  color: #E1D4C9;
  flex: 1;
  align-self: flex-start;
  text-decoration: none;
  border-bottom: 2px solid transparent;
  transition: 0.5s all;
}
.footer_contacts a:hover {
  border-bottom: 2px solid #E1D4C9;
}

@media (max-width: 1439px) {
  .tab_content {
    max-width: 1000px;
  }
}
@media (max-width: 1199px) {
  .mobile_wrapper {
    display: flex;
    flex-direction: column;
    justify-content: start;
  }
  .mobile_column {
    flex-grow: 1;
    width: 100%;
    display: flex;
    justify-content: center;
  }
  .footer_wrapper {
    padding: 100px 60px;
  }
}
@media (max-width: 791px) {
  .header {
    position: relative;
  }
  .header .link-text {
    font-size: 32px;
    line-height: 125%;
  }
  .header_wrapper {
    display: block;
  }
  .header_nav {
    padding-top: 60px;
    z-index: 9;
    width: 100vw;
    height: 100vh;
    position: fixed;
    right: -100%;
    font-size: 32px;
    line-height: 125%;
    transition: 0.5s all;
  }
  .header_nav_open {
    top: 100px;
    right: 0;
    visibility: visible;
    opacity: 1;
  }
  .header_menu-link {
    z-index: 9;
    position: fixed;
    gap: 30px;
    right: -50%;
    top: 600px;
    transition: 0.5s right;
  }
  .header_menu-link .menu-link_link {
    gap: 16px;
  }
  .header_menu-link .menu-link_icon svg {
    width: 40px;
    height: 40px;
  }
  .header_menu-link_open {
    right: 50%;
    transform: translateX(50%);
    visibility: visible;
    opacity: 1;
  }
  .header_list {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 60px;
  }
  .header_logo-hamburger-wrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .header_logo-hamburger-wrapper .hamburger {
    display: flex;
    width: 44px;
    height: 44px;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    gap: 8px;
    border-radius: 100px;
    border: 1px solid #665F55;
  }
  .header_logo-hamburger-wrapper .hamburger span {
    display: block;
    width: 16px;
    height: 1.5px;
    background: #403F3D;
    transition: 0.5s all;
  }
  .header_logo-hamburger-wrapper .hamburger_open {
    z-index: 10;
  }
  .header_logo-hamburger-wrapper .hamburger_open span:nth-child(1) {
    transform: translateY(5px) rotate(-45deg);
  }
  .header_logo-hamburger-wrapper .hamburger_open span:nth-child(2) {
    transform: translateY(-5px) rotate(45deg);
  }
  .enjoy {
    padding: 100px 60px;
  }
  .gallery_img {
    max-width: 688px;
  }
  .gallery_img__rectangle {
    display: none;
  }
}
@media (max-width: 767px) {
  .container {
    max-width: 768px;
    padding: 0 16px;
  }
  h2 {
    font-size: 32px;
    line-height: 125%;
  }
  .enjoy {
    padding: 60px 16px;
  }
  .enjoy_title {
    font-size: 42px;
    line-height: 115%;
  }
  .slider_wrapper {
    display: flex;
    flex-direction: column;
    justify-content: center;
    height: 532px;
  }
  .slider_arrow {
    display: none;
  }
  .slider_items {
    max-width: 348px;
  }
  .slider_img {
    height: 348px;
  }
  .slide img {
    height: 348px;
    width: 348px;
  }
  .about_title {
    font-size: 32px;
  }
  .gallery_img {
    height: 590px;
  }
  .gallery_img img {
    height: 120%;
    object-fit: cover;
  }
  .mobile_wrapper {
    gap: 40px;
  }
  .mobile_column {
    width: 348px;
    max-height: 380px;
  }
  .mobile_column_img img {
    height: 348px;
    width: 348px;
  }
  .mobile_apps {
    display: flex;
    flex-direction: column;
    gap: 20px;
  }
  .footer_wrapper {
    padding: 60px 16px;
    gap: 40px;
  }
  .footer_column {
    min-width: 300px;
  }
  .menu_title {
    font-size: 32px;
  }
}